@media (max-width: 1500px) {
  /*Header*/
  .header__menu {
    max-width: 38rem;
  }
}
@media (max-width: 1250px) {
  html {
    font-size: 15px;
  }
  .wrapper {
    width: 95%;
  }
  .swiper-arrow-prev:not(.modal__arrow) {
    right: calc(2.5% + 6rem);
  }
  .swiper-arrow-next:not(.modal__arrow) {
    right: 2.5%;
  }
  /*Trainers*/
  .trainers__slider {
    margin-left: 2.5%;
  }
  /*Media*/
  .media__wrapper > div:last-of-type {
    width: 35%;
  }
  /*Cards*/
  .cards__slider {
    margin-left: 0;
  }
}
@media (max-width: 1150px) {
  /*Header*/
  .header.active .header__menu {
    opacity: 1;
    z-index: 15;
    visibility: visible;
  }
  .header.active .header__menu ul,
  .header.active .header__menu .header__mobile {
    transform: none;
  }
  .header.active .header__menu-wrapper::after {
    transform: none;
  }
  .header__menu {
    width: 100vw;
    max-width: initial;
    height: 100vh;
    overflow: scroll;
    background-color: rgba(0, 0, 0, 0.6);
    position: fixed;
    left: 0;
    top: 0;
    transition: .3s;
    opacity: 0;
    z-index: -15;
    visibility: hidden;
  }
  .header__menu-wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    padding: 2.5%;
  }
  .header__menu-wrapper::after {
    content: '';
    width: 50%;
    height: 100%;
    background-color: #323a45;
    position: absolute;
    left: 0;
    top: 0;
    transform: translateX(-100%);
    transition: .5s;
  }
  .header__menu ul {
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    z-index: 2;
    transform: translateX(-100%);
    transition: .5s;
  }
  .header__menu ul a {
    display: block;
    font-size: 1.3rem;
    padding-top: .7rem;
    padding-bottom: .7rem;
    text-transform: uppercase;
  }
  .header__mobile {
    display: flex;
    transform: translateX(-100%);
    transition: .5s;
  }
  .btn-menu {
    display: flex;
  }
  /*Stocks*/
  .stocks__container {
    width: 72%;
  }
  .stocks__tabs {
    width: 25%;
  }
  .stocks__tabs-item {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .stocks__tabs-item span {
    position: relative;
    left: 0!important;
    right: 0!important;
    top: 0;
    text-align: left;
  }
  .stocks__tabs-item span:first-of-type {
    margin-bottom: 1rem;
  }
  .stocks__tabs-item::after {
    background: linear-gradient(to right, #000, rgba(0, 0, 0, 0.4));
  }
  /*About*/
  .about__image {
    width: 150%;
  }
}
@media (max-width: 1000px) {
  /*Services*/
  .services__tabs {
    margin-bottom: 2rem;
  }
  .services__tabs-item {
    display: inline-block;
    padding: .6rem 1.2rem;
    border: 1px solid #d5d5d5;
    border-radius: 3rem;
    margin-bottom: 1.1rem;
  }
  .services__tabs-item.active {
    background-color: #bef52b;
    border-color: #bef52b;
  }
  .services__tabs-item.active::after {
    display: none;
  }
  .services__item {
    display: flex;
    flex-direction: column;
  }
  .services__item p {
    font-size: 1rem;
  }
  .services__slider {
    position: static;
    transform: none;
    order: -1;
    margin-bottom: 1.5rem;
    margin-left: 0;
    margin-right: 0;
  }
  .services__slider > div {
    flex-wrap: nowrap;
  }
  .services__text {
    width: calc(100vw - 10%);
  }
  /*Gallery*/
  .gallery__slider-item {
    width: 22rem;
  }
  .gallery__image {
    height: 16rem;
  }
}
@media (max-width: 900px) {
  .wrapper {
    width: 90%;
  }
  /*Header*/
  .header__menu-wrapper {
    padding: 2rem 5% 3rem 5%;
  }
  /*Stocks*/
  .stocks__container {
    width: 100%;
    height: 35rem;
    margin-bottom: 2rem;
  }
  .stocks__tabs {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .stocks__tabs-item {
    width: 31%;
    height: 9rem;
  }
  .stocks__tabs-item:not(:last-of-type) {
    margin-bottom: 0;
  }
  /*Trainers*/
  .trainers__button {
    background-color: #bef52b;
    bottom: 1.5rem;
    opacity: 1;
  }
  .trainers .swiper-slide-active + div .trainers__slider-wrapper {
    background-color: #bef52b;
  }
  .trainers .swiper-slide-active + div .trainers__button {
    background-color: #fff;
  }
  .trainers__slider {
    margin-left: 5%;
  }
  .trainers__slider-item:hover .trainers__slider-wrapper {
    background-color: #fff;
    box-shadow: none;
  }
  /*Media*/
  .media__wrapper > div:first-of-type {
    width: 56%;
  }
  .media__wrapper > div:last-of-type {
    width: 40%;
  }
  .media__title {
    font-size: 2rem;
  }
  .media__video {
    height: 19rem;
  }
  .media__inst-item {
    height: 6rem;
  }
  /*Cards*/
  .cards__slider-item {
    width: 30rem;
  }
  .cards__info {
    width: 40%;
  }
  .cards__paragraph {
    width: 100%;
  }
  .cards__cover {
    width: 58%;
  }
  /*About*/
  .about__image {
    width: 200%;
  }
  .about__title {
    margin-bottom: 15rem;
  }
  .about__text {
    width: 80%;
    top: 6rem;
  }
  /*Footer*/
  .footer__wrapper > p,
  .footer__wrapper > a:not(.footer__up) {
    width: 42%;
    margin-bottom: 1.5rem;
  }
  /*Modal*/
  .modal__wrapper {
    max-width: 32rem;
  }
  .modal__special .modal__title {
    font-size: 1.8rem;
  }
  .modal__card {
    width: 80%;
  }
  .modal__slider-item {
    height: 27rem;
  }
}
@media (max-width: 750px) {
  /*Header*/
  .header__menu-wrapper::after {
    width: 65%;
  }
  .header__contact {
    flex-grow: 2;
    text-align: right;
    margin-right: 2rem;
  }
  .header__contact .header__button {
    display: none;
  }
  /*Stocks*/
  .stocks__container {
    height: 27rem;
  }
  .stocks__tabs-item {
    height: 7rem;
  }
  /*Services*/
  .services {
    padding-top: 4rem;
  }
  .services__title {
    margin-bottom: 3rem;
  }
  .services__tabs {
    margin-left: -0.5rem;
    margin-right: -0.5rem;
  }
  .services__tabs-item {
    margin-left: .5rem;
    margin-right: .5rem;
  }
  .services__slider-item {
    width: 15rem;
    height: 11rem;
  }
  /*Trainers*/
  .trainers {
    padding-top: 4rem;
    padding-bottom: 7rem;
  }
  /*Media*/
  .media {
    padding-bottom: 4rem;
  }
  .media__wrapper > div {
    width: 100%!important;
  }
  .media__wrapper > div:last-of-type {
    align-items: center;
  }
  .media__title {
    margin-bottom: 3rem;
  }
  .media__video {
    height: 17rem;
    margin-bottom: 4rem;
  }
  .media__subtitle {
    align-self: flex-end;
  }
  .media__inst-item {
    height: 10rem;
  }
  /*Cards*/
  .cards {
    padding-top: 2rem;
    padding-bottom: 7rem;
  }
  /*Gallery*/
  .gallery {
    padding-top: 4rem;
    padding-bottom: 7rem;
  }
  .gallery__title {
    margin-bottom: 3rem;
  }
  .gallery__slider-item {
    width: 20rem;
  }
  .gallery__image {
    height: 14rem;
  }
  /*About*/
  .about {
    padding-top: 4rem;
  }
  .about__title {
    margin-bottom: 3rem;
  }
  .about__text {
    width: 100%;
    position: static;
    margin-bottom: -3rem;
  }
  /*Contact*/
  .contact {
    display: flex;
    flex-direction: column;
    background-color: #323a45;
    padding-bottom: 0;
  }
  .contact__wrapper {
    order: -1;
  }
  .contact .form {
    margin-top: 0;
    width: 100%;
  }
  .contact__map {
    position: static;
    height: 24rem;
    overflow: hidden;
  }
  /*Footer*/
  .footer {
    background-color: #323a45;
    color: #fff;
  }
  .footer a {
    color: #fff;
  }
  .footer__wrapper > p,
  .footer__wrapper > a {
    display: block;
    margin-bottom: 2rem;
  }
  .footer__wrapper > a:first-of-type {
    background-image: url(../img/footer-adress-white.svg);
  }
  .footer__wrapper > p:nth-of-type(1) {
    background-image: url(../img/footer-time-white.svg);
  }
  .footer__wrapper > p:nth-of-type(2) {
    background-image: url(../img/footer-phone-white.svg);
  }
  /*Modal*/
  .modal__special {
    align-items: flex-start;
    padding-top: 3rem;
  }
  .modal__special::after {
    display: none;
  }
  .modal__special .modal__title {
    font-size: 1.6rem;
    padding-bottom: 3rem;
  }
  .modal__special-row {
    flex-wrap: wrap;
  }
  .modal__special-item {
    width: 100%;
  }
  .modal__special-item:not(:last-of-type) {
    margin-bottom: 1.3rem;
  }
  .modal__special .modal__form {
    flex-direction: column;
    margin-bottom: 1rem;
  }
  .modal__special .form__input-wrap {
    margin-right: 0;
  }
  .modal__special .form__input-wrap:not(:last-of-type) {
    margin-bottom: 1rem;
  }
  .modal__special .form__agreement {
    width: 100%;
    position: static;
    margin-bottom: 2rem;
  }
  .modal__special .form__button {
    width: 100%;
    margin-bottom: 2rem!important;
  }
}
@media (max-width: 500px) {
  html {
    font-size: 14.5px;
  }
  .button {
    padding-bottom: .9rem;
    font-size: 1.1rem;
    min-height: 3.8rem;
    min-width: 15rem;
    max-width: 100%;
  }
  /*Header*/
  .header__logo {
    width: 6rem;
    height: 2.5rem;
  }
  .header__menu-wrapper::after {
    width: 70%;
  }
  .header__phone {
    font-size: 1rem;
  }
  /*Stocks*/
  .stocks {
    padding-bottom: 4rem;
  }
  .stocks__container {
    height: 21rem;
  }
  .stocks__info {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  .stocks__info_date {
    left: 1rem;
  }
  .stocks__info_other {
    right: 1rem;
    font-size: .7rem;
  }
  .stocks__tabs-item {
    height: 5.5rem;
  }
  .stocks__tabs-item span:last-of-type {
    text-transform: none;
  }
  .stocks__button {
    right: 1rem;
    bottom: 2rem;
  }
  /*Services*/
  .services__tabs-item {
    font-size: 1rem;
  }
  .services__text {
    width: calc(100vw - 5%);
  }
  /*Trainers*/
  .trainers__image {
    width: 100%;
  }
  .trainers__slider-item {
    width: 18rem;
  }
  .trainers__button {
    min-width: 13rem;
  }
  .trainers .swiper-slide-active .trainers__slider-wrapper {
    background-color: #bef52b;
  }
  .trainers .swiper-slide-active .trainers__button {
    background-color: #fff;
  }
  .trainers .swiper-slide-active + div .trainers__slider-wrapper {
    background-color: #fff;
  }
  .trainers .swiper-slide-active + div .trainers__button {
    background-color: #bef52b;
  }
  /*Media*/
  .media__inst-item {
    height: 9rem;
  }
  /*Cards*/
  .cards__slider {
    margin-left: 0;
    padding-left: 0;
  }
  .cards__slider-item {
    width: 24rem;
  }
  .cards__slider-wrapper {
    flex-wrap: wrap;
    padding: 0;
  }
  .cards__cover {
    order: -1;
    width: 100%;
    margin-bottom: 2rem;
  }
  .cards__cover:not(.modal__card) {
    border-radius: 0;
  }
  .cards__info {
    width: 100%;
    padding: 0 1.8rem 2rem 1.8rem;
    align-items: center;
  }
  .cards__paragraph {
    width: 49%;
    font-size: 1rem;
  }
  .cards.cards__description {
    font-size: 1rem;
  }
  .cards__button {
    min-width: 49%;
    min-height: initial;
  }
  /*Gallery*/
  .gallery__image:first-of-type {
    margin-bottom: 15px;
  }
  .gallery__slider {
    padding-left: 15px;
  }
  /*About*/
  .about__text {
    font-size: 1.1rem;
  }
  /*Contact*/
  .contact .form {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  /*Footer*/
  .footer__wrapper {
    position: relative;
  }
  .footer__wrapper > p,
  .footer__wrapper > a:not(.footer__up) {
    width: 100%;
  }
  .footer__up {
    margin-bottom: 0;
    position: absolute;
    right: 0;
    top: 0;
  }
  .footer .social {
    width: 50%;
  }
  /*Forms*/
  .form__button {
    width: 100%;
  }
  /*Modal*/
  .modal_cards .modal__header {
    padding-bottom: 1rem;
  }
  .modal_video {
    width: 100%;
  }
  .modal_gallery {
    width: 100%;
  }
  .modal__header {
    padding: 3.5rem 2.5rem 2.5rem;
  }
  .modal__form {
    padding: 2.5rem 2.5rem 0 2.5rem;
  }
  .modal__form .form__button {
    margin-bottom: 2.5rem;
  }
  .modal__card {
    width: 90%;
  }
  .modal__slider-item {
    height: 19rem;
    border-radius: 0;
  }
  .modal__close {
    top: 1rem;
    right: 1rem;
  }
}
@media (max-width: 400px) {
  html {
    font-size: 13.5px;
  }
}
@media (max-width: 370px) {
  html {
    font-size: 12.5px;
  }
}
