@theme-color: #bef52b;
@grey-color: #323a45;
@dark-color: #252b33;
@text-color: #333;
@light-color: #ededed;


@media (max-width: 1500px) {

  /*Header*/

  .header {
    &__menu {
      max-width: 38rem;
    }
  }

}


@media (max-width: 1250px) {

  html {
    font-size: 15px;
  }

  .wrapper {
    width: 95%;
  }

  .swiper-arrow {
    &-prev:not(.modal__arrow) {
      right: ~'calc(2.5% + 6rem)';
    }
    &-next:not(.modal__arrow) {
      right: 2.5%;
    }
  }

  /*Trainers*/

  .trainers {
    &__slider {
      margin-left: 2.5%;
    }
  }

  /*Media*/

  .media {
    &__wrapper {
      >div:last-of-type {
        width: 35%;
      }
    }
  }

  /*Cards*/

  .cards {
    &__slider {
      margin-left: 0;
    }
  }

}


@media (max-width: 1150px) {

  /*Header*/

  .header {
    &.active {
      .header__menu {
        opacity: 1;
        z-index: 15;
        visibility: visible;
        ul,
        .header__mobile {
          transform: none;
        }
        &-wrapper {
          &::after {
            transform: none;
          }
        }
      }
    }
    &__menu {
      width: 100vw;
      max-width: initial;
      height: 100vh;
      overflow: scroll;
      background-color: rgba(0,0,0,.6);
      position: fixed;
      left: 0;
      top: 0;
      transition: .3s;
      opacity: 0;
      z-index: -15;
      visibility: hidden;
      &-wrapper {
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        padding: 2.5%;
        &::after {
          content: '';
          width: 50%;
          height: 100%;
          background-color: @grey-color;
          position: absolute;
          left: 0;
          top: 0;
          transform: translateX(-100%);
          transition: .5s;
        }
      }
      ul {
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        z-index: 2;
        transform: translateX(-100%);
        transition: .5s;
        a {
          display: block;
          font-size: 1.3rem;
          padding-top: .7rem;
          padding-bottom: .7rem;
          text-transform: uppercase;
        }
      }
    }
    &__mobile {
      display: flex;
      transform: translateX(-100%);
      transition: .5s;
    }
  }

  .btn-menu {
    display: flex;
  }

  /*Stocks*/

  .stocks {
    &__container {
      width: 72%;
    }
    &__tabs {
      width: 25%;
      &-item {
        padding: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        span {
          position: relative;
          left: 0!important;
          right: 0!important;
          top: 0;
          text-align: left;
          &:first-of-type {
            margin-bottom: 1rem;
          }
        }
        &::after {
          background: linear-gradient(to right, #000, rgba(0, 0, 0, 0.4));
        }
      }
    }
  }

  /*About*/

  .about {
    &__image {
      width: 150%;
    }
  }

}


@media (max-width: 1000px) {

  /*Services*/

  .services {
    &__tabs {
      margin-bottom: 2rem;
      &-item {
        display: inline-block;
        padding: .6rem 1.2rem;
        border: 1px solid #d5d5d5;
        border-radius: 3rem;
        margin-bottom: 1.1rem;
        &.active {
          background-color: @theme-color;
          border-color: @theme-color;
          &::after {
            display: none;
          }
        }
      }
    }
    &__item {
      display: flex;
      flex-direction: column;
      p {
        font-size: 1rem;
      }
    }
    &__slider {
      position: static;
      transform: none;
      order: -1;
      margin-bottom: 1.5rem;
      margin-left: 0;
      margin-right: 0;
      >div {
        flex-wrap: nowrap;
      }
    }
    &__text {
      width: ~'calc(100vw - 10%)';
    }
  }

  /*Gallery*/

  .gallery {
    &__slider {
      &-item {
        width: 22rem;
      }
    }
    &__image {
      height: 16rem;
    }
  }

}


@media (max-width: 900px) {

  .wrapper {
    width: 90%;
  }

  /*Header*/

  .header {
    &__menu-wrapper {
      padding: 2rem 5% 3rem 5%;
    }
  }

  /*Stocks*/

  .stocks {
    &__container {
      width: 100%;
      height: 35rem;
      margin-bottom: 2rem;
    }
    &__tabs {
      width: 100%;
      display: flex;
      justify-content: space-between;
      &-item {
        width: 31%;
        height: 9rem;
        &:not(:last-of-type) {
          margin-bottom: 0;
        }
      }
    }
  }

  /*Trainers*/

  .trainers {
    &__button {
      background-color: @theme-color;
      bottom: 1.5rem;
      opacity: 1;
    }
    .swiper-slide-active + div {
      .trainers__slider-wrapper {
        background-color: @theme-color;
      }
      .trainers__button {
        background-color: #fff;
      }
    }
    &__slider {
      margin-left: 5%;
      &-item {
        &:hover {
          .trainers__slider-wrapper {
            background-color: #fff;
            box-shadow: none;
          }
        }
      }
    }
  }

  /*Media*/

  .media {
    &__wrapper {
      >div {
        &:first-of-type {
          width: 56%;
        }
        &:last-of-type {
          width: 40%;
        }
      }
    }
    &__title {
      font-size: 2rem;
    }
    &__video {
      height: 19rem;
    }
    &__inst {
      &-item {
        height: 6rem;
      }
    }
  }

  /*Cards*/

  .cards {
    &__slider {
      &-item {
        width: 30rem;
      }
    }
    &__info {
      width: 40%;
    }
    &__paragraph {
      width: 100%;
    }
    &__cover {
      width: 58%;
    }
  }

  /*About*/

  .about {
    &__image {
      width: 200%;
    }
    &__title {
      margin-bottom: 15rem;
    }
    &__text {
      width: 80%;
      top: 6rem;
    }
  }

  /*Footer*/

  .footer {
    &__wrapper {
      > p,
      > a:not(.footer__up) {
        width: 42%;
        margin-bottom: 1.5rem;
      }
    }
  }

  /*Modal*/

  .modal {
    &__wrapper {
      max-width: 32rem;
    }
    &__special {
      .modal__title {
        font-size: 1.8rem;
      }
    }
    &__card {
      width: 80%;
    }
    &__slider {
      &-item {
        height: 27rem;
      }
    }
  }

}


@media (max-width: 750px) {

  /*Header*/

  .header {
    &__menu-wrapper {
      &::after {
        width: 65%;
      }
    }
    &__contact {
      flex-grow: 2;
      text-align: right;
      margin-right: 2rem;
      .header__button {
        display: none;
      }
    }
  }

  /*Stocks*/

  .stocks {
    &__container {
      height: 27rem;
    }
    &__tabs {
      &-item {
        height: 7rem;
      }
    }
  }

  /*Services*/

  .services {
    padding-top: 4rem;
    &__title {
      margin-bottom: 3rem;
    }
    &__tabs {
      margin-left: -.5rem;
      margin-right: -.5rem;
      &-item {
        margin-left: .5rem;
        margin-right: .5rem;
      }
    }
    &__slider {
      &-item {
        width: 15rem;
        height: 11rem;
      }
    }
  }

  /*Trainers*/

  .trainers {
    padding-top: 4rem;
    padding-bottom: 7rem;
  }

  /*Media*/

  .media {
    padding-bottom: 4rem;
    &__wrapper {
      >div {
        width: 100%!important;
        &:last-of-type {
          align-items: center;
        }
      }
    }
    &__title {
      margin-bottom: 3rem;
    }
    &__video {
      height: 17rem;
      margin-bottom: 4rem;
    }
    &__subtitle {
      align-self: flex-end;
    }
    &__inst {
      &-item {
        height: 10rem;
      }
    }
  }

  /*Cards*/

  .cards {
    padding-top: 2rem;
    padding-bottom: 7rem;
  }

  /*Gallery*/

  .gallery {
    padding-top: 4rem;
    padding-bottom: 7rem;
    &__title {
      margin-bottom: 3rem;
    }
    &__slider {
      &-item {
        width: 20rem;
      }
    }
    &__image {
      height: 14rem;
    }
  }

  /*About*/

  .about {
    padding-top: 4rem;
    &__title {
      margin-bottom: 3rem;
    }
    &__text {
      width: 100%;
      position: static;
      margin-bottom: -3rem;
    }
  }

  /*Contact*/

  .contact {
    display: flex;
    flex-direction: column;
    background-color: @grey-color;
    padding-bottom: 0;
    &__wrapper {
      order: -1;
    }
    .form {
      margin-top: 0;
      width: 100%;
    }
    &__map {
      position: static;
      height: 24rem;
      overflow: hidden;
    }
  }

  /*Footer*/

  .footer {
    background-color: @grey-color;
    color: #fff;
    a {
      color: #fff;
    }
    &__wrapper {
      >p,
      >a {
        display: block;
        margin-bottom: 2rem;
      }
      >a:first-of-type {
        background-image: url(../img/footer-adress-white.svg);
      }
      >p:nth-of-type(1) {
        background-image: url(../img/footer-time-white.svg);
      }
      >p:nth-of-type(2) {
        background-image: url(../img/footer-phone-white.svg);
      }
    }
  }

  /*Modal*/

  .modal {
    &__special {
      align-items: flex-start;
      padding-top: 3rem;
      &::after {
        display: none;
      }
      .modal__title {
        font-size: 1.6rem;
        padding-bottom: 3rem;
      }
      &-row {
        flex-wrap: wrap;
      }
      &-item {
        width: 100%;
        &:not(:last-of-type) {
          margin-bottom: 1.3rem;
        }
      }
      .modal__form {
        flex-direction: column;
        margin-bottom: 1rem;
      }
      .form__input {
        &-wrap {
          margin-right: 0;
          &:not(:last-of-type) {
            margin-bottom: 1rem;
          }
        }
      }
      .form__agreement {
        width: 100%;
        position: static;
        margin-bottom: 2rem;
      }
      .form__button {
        width: 100%;
        margin-bottom: 2rem!important;
      }
    }
  }

}


@media (max-width: 500px) {

  html {
    font-size: 14.5px;
  }

  .button {
    padding-bottom: .9rem;
    font-size: 1.1rem;
    min-height: 3.8rem;
    min-width: 15rem;
    max-width: 100%;
  }

  /*Header*/

  .header {
    &__logo {
      width: 6rem;
      height: 2.5rem;
    }
    &__menu {
      &-wrapper {
        &::after {
          width: 70%;
        }
      }
    }
    &__phone {
      font-size: 1rem;
    }
  }

  /*Stocks*/

  .stocks {
    padding-bottom: 4rem;
    &__container {
      height: 21rem;
    }
    &__info {
      padding-left: 1rem;
      padding-right: 1rem;
      &_date {
        left: 1rem;
      }
      &_other {
        right: 1rem;
        font-size: .7rem;
      }
    }
    &__tabs {
      &-item {
        height: 5.5rem;
        span {
          &:last-of-type {
            text-transform: none;
          }
        }
      }
    }
    &__button {
      right: 1rem;
      bottom: 2rem;
    }
  }

  /*Services*/

  .services {
    &__tabs {
      &-item {
        font-size: 1rem;
      }
    }
    &__text {
      width: ~'calc(100vw - 5%)';
    }
  }

  /*Trainers*/

  .trainers {
    &__image {
      width: 100%;
    }
    &__slider {
      &-item {
        width: 18rem;
      }
    }
    &__button {
      min-width: 13rem;
    }
    .swiper-slide-active {
      .trainers__slider-wrapper {
        background-color: @theme-color;
      }
      .trainers__button {
        background-color: #fff;
      }
      + div {
        .trainers__slider-wrapper {
          background-color: #fff;
        }
        .trainers__button {
          background-color: @theme-color;
        }
      }
    }
  }

  /*Media*/

  .media {
    &__inst {
      &-item {
        height: 9rem;
      }
    }
  }

  /*Cards*/

  .cards {
    &__slider {
      margin-left: 0;
      padding-left: 0;
      &-item {
        width: 24rem;
      }
      &-wrapper {
        flex-wrap: wrap;
        padding: 0;
      }
    }
    &__cover {
      order: -1;
      width: 100%;
      margin-bottom: 2rem;
      &:not(.modal__card) {
        border-radius: 0;
      }
    }
    &__info {
      width: 100%;
      padding: 0 1.8rem 2rem 1.8rem;
      align-items: center;
    }
    &__paragraph {
      width: 49%;
      font-size: 1rem;
    }
    &.cards__description {
      font-size: 1rem;
    }
    &__button {
      min-width: 49%;
      min-height: initial;
    }
  }

  /*Gallery*/

  .gallery {
    &__image {
      &:first-of-type {
        margin-bottom: 15px;
      }
    }
    &__slider {
      padding-left: 15px;
    }
  }

  /*About*/

  .about {
    &__text {
      font-size: 1.1rem;
    }
  }

  /*Contact*/

  .contact {
    .form {
      padding-left: 1rem;
      padding-right: 1rem;
    }
  }

  /*Footer*/

  .footer {
    &__wrapper {
      position: relative;
      > p,
      > a:not(.footer__up) {
        width: 100%;
      }
    }
    &__up {
      margin-bottom: 0;
      position: absolute;
      right: 0;
      top: 0;
    }
    .social {
      width: 50%;
    }
  }

  /*Forms*/

  .form {
    &__button {
      width: 100%;
    }
  }

  /*Modal*/

  .modal {
    &_cards {
      .modal__header {
        padding-bottom: 1rem;
      }
    }
    &_video {
      width: 100%;
    }
    &_gallery {
      width: 100%;
    }
    &__header {
      padding: 3.5rem 2.5rem 2.5rem;
    }
    &__form {
      padding: 2.5rem 2.5rem 0 2.5rem;
      .form__button {
        margin-bottom: 2.5rem;
      }
    }
    &__card {
      width: 90%;
    }
    &__slider {
      &-item {
        height: 19rem;
        border-radius: 0;
      }
    }
    &__close {
      top: 1rem;
      right: 1rem;
    }
  }

}


@media (max-width: 400px) {

  html {
    font-size: 13.5px;
  }

}


@media (max-width: 370px) {

  html {
    font-size: 12.5px;
  }

}